import s from "./Users.module.css";
import {NavLink} from "react-router-dom";
import userAvatar from "../../../assets/profile-02-512.png";
import React from "react";

let User = (props)=>{
    return(
        <div className={s.item}>
            <div className={s.avatar}>
                <NavLink onClick={() => {

                }} to={`/profile/${props.el.id}`}>
                    <img src={props.el.photos.small != null ?
                        props.el.photos.small : userAvatar} alt=""/>
                </NavLink>
            </div>
            <div className={s.ToogleFollowbox}>
                <button disabled={props.usersInProgressFollowing.some(id => id == props.el.id)}
                        className={props.el.followed ?
                            `${s.ToogleFollowBtn + " " + s.follow}` :
                            `${s.ToogleFollowBtn + " " + s.unfollow}`}
                        onClick={(e) => {
                            props.toogleFollowStatus(props.el)
                        }}>
                    {props.el.followed ? <span>Unfollow</span>
                        : <span>Follow</span>}
                </button>
            </div>
            <div className={s.userInfo}>
                <div className={s.fullname}>{props.el.name}</div>
                <div className={s.status}>{"el.status"}</div>
                <div className={s.country}>{"el.country"}</div>
                <div className={s.city}>{"el.city"}</div>
            </div>
        </div>
    )
}

export default User