import React, {useEffect} from "react";
import {connect} from "react-redux";
import {
    getUsers, setCurrentPage,
    toogleFollowingInProgressUser, toogleFollowStatus,
} from "../../../redux/UsersReducer";
import Users from "./Users";
import Preloader from "../../Preloader/Preloader";
import {
    getCurrentPage,
    getIsAuth, getIsFollowingInProgress,
    getIsLoading,
    getNumberOfUsersOnPage,
    getTotalUsersCount, getUsersInProgressFollowing
} from "../../../Selectors/Selectors";


let UsersApiContainer = (props) => {
    useEffect(() => {
        props.getUsers(props.numberOfUsersOnPage, props.currentPage)
    }, [props.currentPage])
    return <>
        {props.isLoading ?
            <Preloader/> : ""}
        <Users getUsers={props.getUsers}
               usersData={props.usersData}
               setCurrentPage={props.setCurrentPage}
               currentPage={props.currentPage}
               numberOfPages={props.numberOfPages}
               toogleFollowStatus={props.toogleFollowStatus}
               isLoading={props.isLoading}
               usersInProgressFollowing={props.usersInProgressFollowing}
        />
    </>

}

const mapStateToProps = (state) => {
    return {
        usersData: state.Users.usersData,
        numberOfPages: Math.ceil(getTotalUsersCount(state) / getNumberOfUsersOnPage(state)),
        numberOfUsersOnPage: getNumberOfUsersOnPage(state),
        currentPage: getCurrentPage(state),
        totalUsersCount: getTotalUsersCount(state),
        isLoading: getIsLoading(state),
        isAuth: getIsAuth(state),
        isFollowingInProgress: getIsFollowingInProgress(state),
        usersInProgressFollowing: getUsersInProgressFollowing(state)
    }
}
const UsersContainer = connect(mapStateToProps, {
    toogleFollowingInProgressUser,
    setCurrentPage,
    getUsers,
    toogleFollowStatus
})(UsersApiContainer);

export default UsersContainer;
