import React from "react";
import s from "./Users.module.css";
import Paginator from "../../common/Paginator";
import User from "./User";

let Users = (props) => {
    return (
        <div className={s.UsersPage}>
            <div className={s.title}>Users</div>
            <div className={s.UsersField}>
                <div className={s.UsersField}>
                    {props.usersData.map(el =>
                        <User el={el}
                              setAnotherUserProfile={props.setAnotherUserProfile}
                              usersInProgressFollowing={props.usersInProgressFollowing}
                              toogleFollowStatus={props.toogleFollowStatus}
                        />
                    )}
                </div>
            </div>
            <Paginator numberOfPages={props.numberOfPages} currentPage={props.currentPage}
                       setCurrentPage={props.setCurrentPage} getUsers={props.getUsers}/>
        </div>
    )
}

export default Users;
